.left-side{
    margin-left: 0;
    top: 60px;
    background: rgba(255, 255, 255, 0.9);
    width: 25%;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    position: sticky;
    left: 0;
    z-index: 5;
    padding: 10px;
    height: 400px;
}

.left-side_avatar {
    margin-top: 10px;
    padding: 7px 5px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    background: #eaeaee;
    cursor: pointer;
    text-decoration: none;
    color: black;
}
.left-side_avatar-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.left-side_ul{
    padding: 7px 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.left-side_ul li{
    border-radius: 10px;
    list-style-type: none;
    padding: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    width: 100%;
}
.left-side_link {
    display: flex;
    width: 100%;
    text-decoration: none;
    color: #000;
}
.left-side_ul li img{
    margin-left: 20%;
}
.left-side_ul li span{
    margin-left: 5%;
}
.left-side_ul li:hover {
    background: #eaeaee;
}
.left-side_item {
    height: 30px;
    width: 30px;
    margin-left: 10px;
    margin-right: 10px;
}