.auth{
    background: #ffffff;
    position: relative;
    max-width: 850px;
    width: 100%;
    padding: 40px 30px;
    box-shadow: 0 5px 10px rgba(0,0,0,0.2);
    -webkit-perspective: 2700px;
            perspective: 2700px;
    margin: auto;
}
.auth .auth--flip{
    display: none;
}
.auth .auth--cover{
    position: absolute;
    top: 0;
    left: 50%;
    height: 100%;
    width: 50%;
    z-index: 10;
    transition: all 1s ease;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.auth .auth--flip:checked ~ .auth--cover{
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}
.auth .auth--cover img{
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: 12;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.auth .auth--cover .auth--back .backImg{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    transform: rotateY(180deg);
}
.auth .auth--content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.auth--label {
    cursor: pointer;
    color: #349eff;
}

.auth--signup span{
    margin-left: 25px;
}
.auth--login {
    width: 45%;
}
.auth--signup {
    width: 50%;
}
.login--form {
    height: 100%;
    width: 100%;
    background: #fff;
    margin-bottom: 10px;
}

.auth--title {
    position: relative;
    font-size: 30px;
    font-weight: 500;
    color: #333;
}

.auth--input-boxes{
    margin-top: 15px;
}

.auth--input-box{
    display: flex;
    align-items: center;
    height: 50px;
    width: 100%;
    margin: 10px 0;
    position: relative;
}

.auth--input-box input{
    height: 100%;
    width: 100%;
    outline: none;
    border: none;
    padding: 0px 30px;
    font-size: 16px;
    font-weight: 500;
    border-bottom: 2px solid rgba(0,0,0,0.2);
    transition: all 0.3s ease;
}

.auth--input-box input:focus,
.auth--input-box input:valid{
    border-color: #7d2ae8;
}

.auth--input-box i{
    width: auto;
    position: absolute;
    color: #7d2ae8;
    font-size: 17px;
}

.login--text{
    font-size: 14px;
    font-weight: 500;
    color: #333;
}

.login--text a{
    text-decoration: none;
}

.login--text a:hover{
    text-decoration: underline;
}

.auth--button{
    color: #fff;
    margin-top: 10px;
    width: 100%;
}

.auth--button input{
    height: 35px;
    width: 100%;
    color: #fff;
    background: #7d2ae8;
    border-radius: 6px;
    padding: 0;
    cursor: pointer;
    transition: all 0.4s ease;
}

.auth--button input:hover{
    background: #5b13b9;
}

.signup--form {
    height: 100%;
    width: 95%;
    background: #fff;
    margin-bottom: 10px;
    margin-left: 25px;
}
.signup--gender-details{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.signup--gender{
    display: flex;
    justify-content: center;
    align-items: center;
}
.signup--gender input {
    width: auto;
}
.signup--gender span {
    width: auto;
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}

@media (max-width: 730px) {
    .auth .auth--cover{
        display: none;
    }
    .auth--content .auth--login,
    .auth--content .auth--signup{
        width: 100%;
    }
    .auth--content .auth--signup{
        display: none;
    }
    .auth .auth--flip:checked ~ .auth--content .auth--signup{
        display: block;
    }
    .auth .auth--flip:checked ~ .auth--content .auth--login{
        display: none;
    }
    .auth--signup span{
        margin-left: 0;
    }
    .login--form{
        width: 100%;
    }
    .signup--form{
        width: 100%;
        margin-left: 0;
    }
    .signup--gender span {
        -webkit-transform: translateX(15px);
                transform: translateX(15px);
    }
}

