    }
}

@media (max-width: 768px) {
    .header-logo {
        width: 20%;
    }

    .spaceRight {
        width: 20%;
    }

    .messageBody {
        width: 80%;
    }

    .header-logo--img {
        display: none;
    }

    .header-logo--name {
        margin: auto;
    }

    .header-items {
        width: 30%;
    }

    .search-bar {
        min-width: 200px;
    }

    .left-side {
        width: 20%;
        padding: 5px;
    }

    .left-side_avatar-info {
        font-size: 11px;
    }

    .left-side_avatar-info strong {
