    }

    .left-side_ul li img {
        margin-left: 0px;
    }

    .left-side_ul li span {
        margin-left: 0px;
    }

    .home {
        margin-left: 20%;
    }

    .home-body {
        width: 90%;
    }

    .home-right {
        display: none;
    }

    .profile {
        margin-left: 20%;
        width: 80%;
    }

}

@media (max-width: 660px) {
    .header-logo {
        width: 12%;
        max-width: 70px;
    }

    .spaceRight {
        width: 12%;
    }

    .messageBody {
        width: 88%;
    }

    .header-logo--img {
        display: block;
        margin: auto;
    }

    .header-logo--name {
        display: none;
    }

    .header-search {
        width: 53%;
    }

    .header-items {
        width: 35%;
    }

    .search-bar {
        min-width: 160px;
    }

    .status-modal_form {
        width: 90%;
    }

    .left-side {
        width: 12%;
