        padding: 0px;
    }

    .left-side_avatar-info {
        display: none;
    }

    .left-side_ul li img {
        margin-left: 25%;
    }

    .left-side_ul li span {
        display: none;
    }

    .home {
        margin-left: 12%;
    }

    .profile {
        margin-left: 12%;
        width: 88%;
    }
